.blocks_box {
  /* width */
  ::-webkit-scrollbar {
    width: 5px;
    border-radius: 2px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background: #888;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .view-all{
    padding-top: 30px;
    font-size: 15px;
    cursor: pointer;
    font-family: DINPro-Regular;
  }
}
